
header{
    position: relative;
    background: #fff;
}
span{
    color: #333;
}
.header{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height:.45rem;
    border-bottom: 1px solid #eee;
    text-align: center;
    position: relative;
}
.header a{
    display: block;
}
.header .head-left{
    position: absolute;
    left: .1rem;
    top: .16rem;
    width: .15rem;
    height: .075rem;
    background-size: contain;
}
.head-tit{
    display: block;
    text-align: center;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: .16rem;
    color: #333;
    width:100%;
    line-height:.45rem;
}
.header .head-right{
    position: absolute;
    right: .2rem;
    top: .08rem;
    width: .15rem;
    height: .075rem;
    background-size: contain;
}
.icon-home{
    font-size:.28rem;
}
form{
    margin-bottom: .05rem;
    padding:0 .12rem;
    background: #fff;
}
.cuser{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    border-bottom: 1px solid #eee;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.cuser label{
    line-height: .44rem;
    -webkit-box-flex: 0;
        -ms-flex: 0 0 .89rem;
            flex: 0 0 .89rem;
    color: #999;
}
.cuser input {
    height:.22rem;
    padding:.11rem 0;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    display: block;
    color: #999;
}
.btns{
    margin-top:.4rem;
    padding-bottom:.4rem;
    text-align: center;
}
.btns button{
    width:2rem;
    height:.35rem;
    margin: 0 auto ;
    background: #fe4070;
    color: #fff;
    border-radius: .35rem;
}



